
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.banner {
  background-image: url(./assets/header2.jpg);
  background-size: cover;
  width: calc(100% + 16px);
  position: relative;
  left: -8px;
  top: -60px;
  margin-bottom: -60px;
  height: 300px;
  background-position-y: -320px;
}
.fade-enter-active, .fade-leave-active {
  transition-property: opacity;
  transition-duration: .25s;
}
.fade-enter-active {
  transition-delay: .25s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.header {
  margin-bottom: 70px;
}
.header__nav {
    width: 30%;
    margin: auto;
    text-align: center;
    height: 30px;
}
.header__nav__link {
      display: block;
      float: left;
      line-height: 30px;
      font-size: 16px;
      width: calc(100% / 2);
      color: #2c3e50;
      text-decoration: none;
      transition: .5s;
}
.header__nav__link:hover {
        opacity: .6;
        text-decoration: underline;
}
.header__title {
    font-size: 72px;
    text-align: center;
    font-weight: bold;
    line-height: 150px;
}
@media screen and (max-width: 767px) {
#app {
    margin-top: 20px;
}
.header {
    margin-bottom: 0;
}
.header__title {
      font-size: 42px;
}
.header__nav {
      width: 90%;
}
}



